.chair-dec
{
    margin-top: 100px;
    display: grid;
    grid-template-columns:repeat(4,auto);
    align-items: center; 
    justify-content: center;  
    row-gap: 80px;
    column-gap: 80px;
    margin-bottom: 80px; 

}

.product-chair
{
   
   position: relative;
    width: 220px;
    height: 290px;
    background-color: rgba(226, 222, 219, 1);
    padding-top: 25px;
    cursor: pointer;
    
   
}
.calign
{
    height: 90px;
    gap: 10px;
    background-color: rgb(255, 255, 255);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content:flex-end;
    margin-left: 5px;
    margin-right: 5px;
    margin-top: 35px;
}
.ctocart
{
    /* visibility: hidden; */
    padding: 15px 15px;
    cursor: pointer;
    background-color: black;
    color: white;
    width: 100%;
}
.chairprice
{
    margin-bottom: 8px;
}
