.consec
{
    width: 100%;
    height: 100vh;
    margin-top: 3rem;
    background-color: gainsboro;
    display: flex;
    justify-content: center;
    align-items: center;
}
.consec1
{
    width:75% ;
    height: 80%;
   
    display: flex;
}
.conleft
{
    width: 80%;
    display: flex;

    justify-content:left;
     background-color: white;
}
.conleftsec
{
    width: 40%;
    display: flex;
    margin-left: 9rem;
    margin-top: 4rem;
    flex-direction: column;
    background-color: white;
}
.conleftsec input
{
    padding-top: 10px;
    font-size: 18px;
    padding-left: 8px;
    margin-top: 20px;
border: none;
outline: none;
padding-bottom: 5px;
border-bottom: 2px solid rgb(167, 167, 167);
background-color: white;

}
.conbtn
{
  margin-top: 40px; 
  padding: 15px 0px 15px 0px; 
  background-color: #000000a0;
  color: white;
  border: none;
  font-size: 16px;
  font-weight: 550;
}
.conbtn:hover
{
    background-color: #000000e0;
}
.conleftsec p
{
    text-align: left;
    margin-top: 5px;
    background-color: white;
    font-weight: 500;
}
.conleftsec h1
{
    background-color: white;
    text-align: left;
}
.conright
{
    width: 20%;
    background-color: black;
}