.home
{

    position: relative;
}
.banner
{
    width: 100%;
    margin-top: 3rem;
   margin-bottom: 100px;
}
.content
{
    text-align: left;
    position: absolute;
    top: 10rem;
    left: 5rem;
    background-color:rgba(226, 222, 219, 1) ;
}
.con-txt,.con-para
{
    background-color:rgba(226, 222, 219, 1) ;
    margin-bottom: 20px;
}
.btn
{
    padding: 20px 30px 20px 30px;
    font-size: large;
    font-weight:500;
    background-color: rgb(18, 16, 16);
    border: none;
    color: white;
    cursor: pointer;
}
.home-img
{
    display: grid;
    grid-template-columns:repeat(3,auto);
    align-items: center; 
    justify-content: center;  
    row-gap: 30px;
    column-gap: 80px;
    margin-bottom: 30px; 
}
.hcard-img
{
    width: 260px;
    height: 233px;
    background-color: rgb(224, 220, 212);
   padding-top: 25px;
   
    transition:all ease-out 1s;
   
}
.hcard-img:hover .img
{
    transform: scale(1.07);
}
.img
{
    outline: 1px solid white;
    outline-offset: -8px;
    border-radius: 10px;    
}

