.products
{

    margin-top: 100px;
    display: grid;
    grid-template-columns:repeat(4,auto);
    align-items: center; 
    justify-content: center;  
    row-gap: 80px;
    column-gap: 80px;
    margin-bottom: 80px; 
}
.product-card
{
    
    width: 220px;
    height: 290px;
    background-color: #e2dedb;
    padding-top: 25px;
    cursor: pointer;
    position: relative;
}

.lalign
{
    height: 90px;
    gap: 10px;
    background-color: rgb(255, 255, 255);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content:flex-end;
    margin-left: 5px;
    margin-right: 5px;
    margin-top: 35px;
}
.ltocart
{
    /* visibility: hidden; */
    padding: 15px 15px;
    cursor: pointer;
    background-color: black;
    color: white;
    width: 100%;
}
.lightprice
{
    margin-bottom: 8px;
}
