

.plist 
{
   width: 70%;
   margin-left: 15%;
   
   height: fit-content;
   display: flex;
   align-items: center;
   justify-content: space-evenly;
   overflow: hidden;
   margin-top: 5rem;
   font-size: 20px;
  
}
.pl1,.pl2
{
    cursor: pointer;
}
.pl1 h4:hover
{
    border-bottom: 4px solid black;
}
.pl2 h4:hover
{
    border-bottom: 4px solid black;
}

.pvideo
{
    margin-top: 3rem;
}




