.home-chair
{
    display: grid;
    grid-template-columns:repeat(3,auto);
    align-items: center; 
    justify-content: center;
    row-gap: 30px;
    column-gap: 80px; 
    margin-bottom: 100px;
   
}
.cards_img
{
    width: 260px;
    height: 233px;
    background-color:rgb(224, 220, 212); 
    padding-top: 25px;
    transition: all .75s;
}
.cards_img:hover .ch-img
{
    transform: scale(0.85);
}
.ch-img
{
    outline: 1px solid white;
    outline-offset: -8px;
    border-radius: 10px;    
}