.remove
{
    width: 30px;
    height: 35px;
    position: relative;
    background-color: rgb(153, 177, 177);
    cursor: pointer;
    padding-left: 15px;
    transition: all 2s;
    
}
.remove:hover  .line1
{
    rotate: -40deg;
    
}
.remove:hover  .line2
{
    rotate: 40deg;
   
    top: 0px;
}
.line1
{
    width: 2px;
    height: 20px;
    background-color: black;
  rotate: 90deg;
  margin-top: 5px;
  margin-left: 5px;
}
.line2
{
    position: absolute;
    top: 7px;
    width: 2px;
    height: 20px;
    background-color: black;
    rotate: 90deg;
    margin-left: 5px;
    margin-top: 5px;
}
