.logindec
{
    margin-top: 3rem;
}
button
{
    cursor: pointer;
}
.logbg
{
    width: 100%;
    height: 100vh;
    filter: blur(2px);
    
}
.loginsec
{
    width: 100%;
    height: 100vh;
    position: relative;
}
.logbg1
{
    width: 70%;
    height: 90%;
    background-color: rgb(222, 222, 222);
    position: absolute;
   left: 15%;
   top: 5%;
    display: flex;
}
.logcon
{
    width: 55%;
    height: 100%;
    display: flex;
    
    justify-content: center;
}
.logimg
{
    width: 45%;
    height: 100%;
}
.container
{
    width: 60%;
    height: fit-content;  
}
.container h6
{
    color: dimgrey;
}
.logtitle
{
    margin-top: 15px;
}
.subhead
{
    font-size: 20px;
    margin-top: 10px;
    margin-bottom: 10px;
}
.googlebtn
{
    width: 100%;
    font-size: 16px;
    font-weight: 700;
     padding-top: 5px;
     padding-bottom: 5px;
    text-align: left;
    margin-bottom: 20px;
    margin-top: 10px;
}
.googlebtn img
{
    margin-left: 30px;
    margin-bottom: -3px;
    margin-right: 70px;
}

.logemail
{
    margin-top: 20px;
    text-align: left;
}
 #email
{
    width: 94%;
    padding-top: 7px;
    padding-bottom: 7px;
    font-size: 16px;
    padding-left: 15px;
    margin-top: 5px;
    outline: none;
}
.logpass
{
    text-align: left;
    margin-top: 10px;
}
.logforgot
{
    display: flex;
    margin-top: 10px;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 20px;
}
.logcheck
{
    display: flex;
    gap: 10px;
    font-size: 12px;
}

.logforgot h5
{
    color: rgba(205, 39, 39, 0.89);
    cursor: pointer;
}
.logbtn
{
    width: 100%;
    padding-top: 8px;
    padding-bottom: 8px;
    font-size: 14px;
    margin-bottom: 30px;
    font-weight: 600;
}
.logbtn:hover
{
    background-color: rgb(30, 29, 29);
    color: white;
}
.dont a
{
    color:rgba(205, 39, 39, 0.89) ;
}


